@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap);
html , body   , #root{
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

* , *::after,  *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: fixed;
  margin: 0;
  padding: 0;
}

/* font-family: 'Poor Story', cursive; */
/* font-family: 'Oxygen', sans-serif; */

:root {
  /* fonts */
  --font-1: "Oxygen", sans-serif;
  --font-2: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  /* --font-btn: 'cursive'; */
  --font-welcome: "Poor Story", cursive;
}
.GlobalWrapper {
  text-align: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: fixed;
  margin: 0;
  padding: 0;
  font-family: "Oxygen", sans-serif;
  font-family: var(--font-1);
  --theme-duration: 0.4s;
  background: var(--bgA);
}
#dark {
  --col-1: #0f2537;
  --col-2: #2f49bf;
  --col-3: blue;
  --col-text-1: white;
  --col-text-2: black;
  --filter-icon: invert(1);
  --col-name: rgb(35, 186, 35);
  --col-profile: linear-gradient(
    190deg,
    rgba(48, 73, 191, 1) 0%,
    rgb(51, 27, 98) 100%
  );
  --profile-shadow: 0.1em 0.5em 0.3em 0.5em rgb(1, 15, 37);
  --img-shadow: 0em -0.2em 0.3em 0em rgb(14, 36, 96);
  --col-projects: linear-gradient(
    90deg,
    rgba(48, 73, 191, 1) 0%,
    rgb(51, 27, 98) 100%
  );

  --bgA: #0e171f;
  --bgB: var(--bgA);
  --bgC: var(--bgA);
  --bgD: var(--bgA);
}

#light {
  --filter-icon: invert(0);
  --col-1: #84c3d4;
  --col-2: rgb(61, 82, 136);
  --col-3: rgb(9, 25, 42);
  --col-text-1: rgb(20, 51, 60);
  --col-text-2: white;
  --col-name: rgb(17, 71, 102);

  --col-profile: linear-gradient(0deg, rgb(29, 29, 29) 0%, var(--col-2) 100%);
  --profile-shadow: 0.1em 0.5em 0.8em 0.1em rgb(19, 90, 111);
  --img-shadow: 0em -0.2em 0.3em 0em rgb(14, 36, 96);
  --col-projects: linear-gradient(
    250deg,
    rgb(29, 29, 29) 0%,
    var(--col-2) 100%
  );

  --bgA: #def2fa;
  --bgB: var(--bgA);
  --bgC: var(--bgA);
  --bgD: var(--bgA);
}

/* PrintView */
#PrintView {
  --font-1: Arial, Helvetica, sans-serif;
  --font-2: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  --font-welcome: var(--font-1);
  --col-text-1: black;
  --col-text-2: white;
  --filter-icon: invert(0);
  --col-name: black;
  --bgA: white;
  --bgB: white;
  --bgC: white;
  --bgD: white;
  --print-bg: #111920;
}

#PrintView.GlobalWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--print-bg);
  font-size: 0.5rem;
}
@media screen and (max-width: 450px) {
  #PrintView.GlobalWrapper {
    width: 100vw;
  }
}

/* align the Layout div in the middle */

.Layout {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
}
.Layout header {
  flex-basis: 100%;
  height: 5%;
  position: absolute;
  z-index: 3;
}
.Layout main {
  flex-basis: 100%;
  height: 100%;
  overflow-y: scroll;
}
.Layout footer {
  flex-basis: 100%;
  height: 5%;
}

#PrintView .Layout {
  font-size: 0.9rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  max-width: 40em;
  min-width: 35em;
  height: 97%;
  padding: 1em;
  margin: 1em;
  border: 0.2rem solid var(--col-text-1);
  position: fixed;
  background: var(--col-text-2);
  overflow-x: hidden;
}
#PrintView .Layout > header {
  width: 100%;
}

#PrintView .Layout > main {
  padding-top: 3em;
  width: 100%;
}

#PrintView h1 {
  font-size: 1.5em;
}

#PrintView h2 {
  font-size: 1.1em;
}
#PrintView h3 {
  font-size: 1em;
}

#PrintView p {
  font-size: 0.8em;
}

@media screen and (max-width: 767px) {
  #PrintView .Layout {
    width: 97vw;
  }
}
@media screen and (max-width: 450px) {
  #PrintView .Layout {
    max-width: 100vw;
    min-width: 100vw;
  }
}

.Header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100vw;
  height: 3rem;
