/* for change the toggle theme */
/* filter: invert(); */

.ThemeToggle {
  padding: 0.1rem;
  width: 1.4em;
  height: 1.5em;
  font-size: 1rem;
  border-radius: 50%;
  overflow: hidden;
}

.ThemeToggleWrapper:hover {
  cursor: pointer;
}

.ThemeToggleWrapper {
  display: flex;
  justify-content: center;
  width: 200%;
  height: 100%;
  transition: 1s ease-in-out;
  margin: 0;
  padding: 0;
  position: relative;
  transition: 1s ease-in-out;
  -webkit-animation: toggle_sun var(--theme-duration) alternate;
          animation: toggle_sun var(--theme-duration) alternate;
  left: 0%;
}

.Moon,
.Sun {
  flex-basis: 50%;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.1em;
}
.Moon img,
.Sun img {
  width: 100%;
  height: 100%;
  -webkit-filter: var(--filter-icon);
          filter: var(--filter-icon);
  object-fit: cover;
}
#ThemeSwitch {
  display: none;
}
#ThemeSwitch:checked ~ .ThemeToggleWrapper {
  transition: 1s ease-in-out;
  -webkit-animation: toggle_moon var(--theme-duration) alternate;
          animation: toggle_moon var(--theme-duration) alternate;
  left: -100%;
}

@-webkit-keyframes toggle_sun {
  from {
    left: -100%;
  }
  to {
    left: 0%;
  }
}

@keyframes toggle_sun {
  from {
    left: -100%;
  }
  to {
    left: 0%;
  }
}

@-webkit-keyframes toggle_moon {
  from {
    left: 0%;
  }
  to {
    left: -100%;
  }
}

@keyframes toggle_moon {
  from {
    left: 0%;
  }
  to {
    left: -100%;
  }
}

/* #PrintView  */
#PrintView .ThemeToggle {
  display: none;
}

