.bubbleWrapper {
  position: relative;
  -webkit-animation: move_bubble 20s linear both infinite;
          animation: move_bubble 20s linear both infinite;
}
.bubble {
  font-size: inherit;
  position: absolute;
  width: 5em;
  height: 5em;
  border-radius: 50%;
}

@-webkit-keyframes move_bubble {
  0% {
    left: 0%;
  }
  50% {
    left: 50%;
  }
  100% {
    left: 100%;
  }
}

@keyframes move_bubble {
  0% {
    left: 0%;
  }
  50% {
    left: 50%;
  }
  100% {
    left: 100%;
  }
}
.bubble::before,
.bubble::after {
  position: absolute;
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-animation: bubble_butt 5s linear both infinite;
          animation: bubble_butt 5s linear both infinite;
  border: 0.5em solid var(--col-2);
}

.bubble::after {
  border-color: var(--col-2);
}
.bubble::after {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
@-webkit-keyframes bubble_butt {
  0% {
    -webkit-transform: scale(30%);
            transform: scale(30%);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(90%);
            transform: scale(90%);
  }
}
@keyframes bubble_butt {
  0% {
    -webkit-transform: scale(30%);
            transform: scale(30%);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(90%);
            transform: scale(90%);
  }
}
.bubbleWrapper:nth-child(odd) {
  animation-direction: reverse;
}
.bubbleWrapper:nth-child(1) {
  left: 10%;
  top: 80%;
  font-size: 3em;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.bubbleWrapper:nth-child(2) {
  left: 50%;
  top: 30%;
  font-size: 0.4em;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.bubbleWrapper:nth-child(3) {
  left: 30%;
  top: 60%;
  font-size: 0.8em;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
.bubbleWrapper:nth-child(4) {
  left: 0%;
  top: 50%;
  font-size: 0.2em;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.bubbleWrapper:nth-child(5) {
  left: 35%;
  top: 0%;
  font-size: 1.2em;
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
}
.bubbleWrapper:nth-child(6) {
  left: 75%;
  top: 95%;
  font-size: 1em;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.bubbleWrapper:nth-child(7) {
  left: 48%;
  top: 32%;
  font-size: 0.4em;
  -webkit-animation-delay: 3.5s;
          animation-delay: 3.5s;
}
.bubbleWrapper:nth-child(8) {
  left: 25%;
  top: 80%;
  font-size: 2.2em;
  -webkit-animation-delay: 4s;
