#PrintView .BgB {
  display: none;
}
.BgB {
  font-size: 2em;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background-color: var(--bgB);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.BgB > .ballBgB {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 0.2em;
  gap: 0.2em;
}
.BgB > .ballBgB > div {
  width: 0.4em;
  height: 0.4em;
  border-radius: 50%;
  background-color: var(--col-2);
  -webkit-animation: ball_blink 1.1s ease-in infinite;
          animation: ball_blink 1.1s ease-in infinite;
}
@-webkit-keyframes ball_blink {
  0% {
    background-color: var(--col-2);
    -webkit-transform: translateY(-0.5em);
            transform: translateY(-0.5em);
  }
  50% {
    background-color: var(--col-1);
    -webkit-transform: translateY(0em);
            transform: translateY(0em);
  }
  100% {
    background-color: var(--col-2);
    -webkit-transform: translateY(-0.5em);
            transform: translateY(-0.5em);
  }
}
@keyframes ball_blink {
  0% {
    background-color: var(--col-2);
    -webkit-transform: translateY(-0.5em);
            transform: translateY(-0.5em);
  }
  50% {
    background-color: var(--col-1);
    -webkit-transform: translateY(0em);
            transform: translateY(0em);
  }
  100% {
    background-color: var(--col-2);
    -webkit-transform: translateY(-0.5em);
            transform: translateY(-0.5em);
  }
}
.BgB > .ballBgB > div:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.BgB > .ballBgB > div:nth-child(2) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.BgB > .ballBgB > div:nth-child(3) {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.BgB > .linesBgB {
  width: 100%;
  height: auto;
  padding-bottom: 1em;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.BgB .linesBgB > div {
  background: var(--col-2);
  margin: 0.1em 0;
  width: 90%;
  height: 0.2em;
  border-radius: 1em;
  -webkit-animation: shot_in 4s ease-in-out infinite;
          animation: shot_in 4s ease-in-out infinite;
}

@-webkit-keyframes shot_in {
  0% {
    -webkit-transform: scaleX(70%);
            transform: scaleX(70%);
  }
  50% {
    -webkit-transform: scaleX(0%);
            transform: scaleX(0%);
  }
  100% {
    -webkit-transform: scaleX(70%);
            transform: scaleX(70%);
  }
}

@keyframes shot_in {
  0% {
    -webkit-transform: scaleX(70%);
            transform: scaleX(70%);
  }
  50% {
    -webkit-transform: scaleX(0%);
            transform: scaleX(0%);
  }
  100% {
    -webkit-transform: scaleX(70%);
            transform: scaleX(70%);
  }
}
.BgB .linesBgB > div:nth-child(even) {
  background: var(--col-1);
}
.BgB .linesBgB > div:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.BgB .linesBgB > div:nth-child(2) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.BgB .linesBgB > div:nth-child(3) {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

